// import { PARAM } from 'App';
import { Solution } from './index';
import { Vector, zoomOffset, applyBoundaries, draw, loadImage } from './Util';

const PARAM = undefined;


export class CanvasImage {
    solution: Solution;

    canvas: HTMLCanvasElement;
    statusRendering: Vector;
    statusTarget: Vector = new Vector(200, 200, 4);

    needInterpolation: boolean = false;

    img: HTMLImageElement;
    ctx: CanvasRenderingContext2D;

    speedInterpolation: number = 0.1;
    easingInterpolation: boolean = true;

    constructor(solution: Solution) {
        this.solution = solution;
        // https://gist.github.com/dzhang123/2a3a611b3d75a45a3f41
        // refer: https://codepen.io/NJStudio/pen/poZoEgY?editors=0010
        this.canvas = document.createElement('canvas');
        this.canvas.style.background = '#111';
        // this.canvas.style.width = '100%';
        // this.canvas.style.height = '100%';
        this.canvas.width = 1200;
        this.canvas.height = 800;
        solution.div.appendChild(this.canvas);
        this.ctx = this.canvas.getContext('2d') as CanvasRenderingContext2D;

        let image

        this.statusRendering = new Vector(0, 0, 1)
        let statusPre = new Vector(0, 0, 1)
        let status = new Vector(0, 0, 1)
        let isDrag = false

        this.canvas.addEventListener('mousedown', (e) => {
            const { offsetX, offsetY } = e
            status.x = offsetX
            status.y = offsetY
            // isDrag = true;
            if (status.z > 2) isDrag = true
        });
        this.canvas.addEventListener('mouseup', (e) => {
            if (!isDrag) return
            const { offsetX, offsetY } = e
            const dx = offsetX - status.x
            const dy = offsetY - status.y
            statusPre.x += dx
            statusPre.y += dy
            isDrag = false
        });
        this.canvas.addEventListener('wheel', (e) => {
            const { offsetX, offsetY, deltaY } = e
// console.log(deltaY)
            const oy = deltaY < 0 ? 1 : -1
            status.z += (0.2 * oy)

            if (status.z < 0.2) {
                status.z = 0.2
            } else if (status.z > 5) {
                status.z = 5
            }

            const { x: zx, y: zy } = zoomOffset(this.canvas, status.z, { x: offsetX, y: offsetY }, statusPre)
            statusPre.x = zx;
            statusPre.y = zy;
            statusPre.z = status.z;

            this.statusRendering.x = zx;
            this.statusRendering.y = zy;
            this.statusRendering.z = status.z;

            // draw(ctx, image, this.statusRendering);
        });
        this.canvas.addEventListener('mousemove', (e) => {
            if (!isDrag) return
            const { offsetX, offsetY } = e
            const x = statusPre.x + (offsetX - status.x)
            const y = statusPre.y + (offsetY - status.y)
            const { x: zx, y: zy } = applyBoundaries(this.canvas, statusPre.z, { x, y })

            this.statusRendering.x = zx;
            this.statusRendering.y = zy;
            this.statusRendering.z = statusPre.z;
            // draw(ctx, image, this.statusRendering)
        });

        console.log(PARAM?.url)
        if (PARAM?.url) {
            console.log(PARAM?.url)
            loadImage(PARAM?.url).then(img => { 
                this.img = img as HTMLImageElement;
            });
        } else {
            loadImage().then(img => { 
                this.img = img as HTMLImageElement;
                // this.canvas.width = this.img.width;
                // this.canvas.height = this.img.height;
            });
        }


        this.render();

    }
    public render() {
        requestAnimationFrame(() => { this.render(); });

        if (this.needInterpolation) {
            if (this.easingInterpolation) {
                this.statusRendering = easingMotion(this.statusRendering, this.statusTarget, this.speedInterpolation);
                if (Vector.distance(this.statusRendering, this.statusTarget) < 1) {
                    this.statusRendering.x = this.statusTarget.x
                    this.statusRendering.y = this.statusTarget.y
                    this.statusRendering.z = this.statusTarget.z
                    this.needInterpolation =  false;
                }
            } else {
                this.statusRendering = easingLinear(this.statusRendering, this.statusTarget, this.speedInterpolation);
                // if (Vector.distance(this.statusRendering, this.statusTarget) < 20) {
                if (Vector.distance(this.statusRendering, this.statusTarget) < this.speedInterpolation * 150) {
                    this.statusRendering.x = this.statusTarget.x
                    this.statusRendering.y = this.statusTarget.y
                    this.statusRendering.z = this.statusTarget.z
                    this.needInterpolation =  false;
                }
            }


        }

        draw(this.ctx, this.img, this.statusRendering);
        // draw(this.ctx, this.img)
    }
}

// https://easings.net/
export const easingLinear = (v1: Vector, v2: Vector, speed: number = 1) => {
    const v = Vector.subtract(v2, v1);
    v.normalize();
    // const len = v.length()
    // console.log(len)
    return new Vector(
        v1.x + v.x * speed * 150,
        v1.y + v.y * speed * 150,
        v1.z + v.z * speed * 150,
    );
};
export const easingMotion = (v1: Vector, v2: Vector, speed: number = 1) => {
    const v = Vector.subtract(v2, v1);
    return new Vector(
        v1.x + v.x * speed,
        v1.y + v.y * speed,
        v1.z + v.z * speed,
    );
};