export const loadImage = (url: string = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/person/HaminHaram.jpg') => {
// export const loadImage = (url: string = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/worldmap/worldMap_03.jpg') => {
    return new Promise((resolve, reject) => {
        let img = new Image()
        img.onload = () => {
            resolve(img)
            img.onload = null
            img = null
        }
        img.onerror = (err) => {
            reject(err)
        }
        img.src = url
    });
};