import { Vector } from './Util';
import { Solution } from './index';


export class Controller {
    solution: Solution;
    div: HTMLDivElement;
    currentIndex = -1;
    timeline: Vector[] = [];

    divFrameNum: HTMLDivElement;

    constructor(solution: Solution) {
        this.solution = solution;

        this.div = document.createElement('div');
        this.solution.div.appendChild(this.div);

        const setA = document.createElement('div');
        setA.style.display = 'flex';
        this.div.appendChild(setA);

        const divKeyNew = document.createElement('div');
        setA.appendChild(divKeyNew);
        divKeyNew.textContent = 'Create Key';
        divKeyNew.style.width = '100px';
        divKeyNew.style.color = 'aqua';
        divKeyNew.onclick = () => {
            this.timeline.push(Vector.deepCopy(solution.canvasImage.statusRendering));
            this.currentIndex = this.timeline.length - 1;

            this.updateFrameNumber();
        }
        const divKeyUpdate = document.createElement('div');
        setA.appendChild(divKeyUpdate);
        divKeyUpdate.textContent = 'Update Key';
        divKeyUpdate.style.width = '100px';
        divKeyUpdate.style.color = 'aqua';
        divKeyUpdate.onclick = () => {

            this.timeline[this.currentIndex] = Vector.deepCopy(solution.canvasImage.statusRendering);
            // console.log('update frame');
        }
        const divPre = document.createElement('div');
        setA.appendChild(divPre);
        divPre.textContent = '<<<';
        divPre.style.paddingRight = '20px'
        divPre.style.color = 'aqua';
        divPre.onclick = () => {
            // console.log(this.solution.canvasImage.statusRendering);
            this.currentIndex--;
            if (this.currentIndex < 0) {
                this.currentIndex = 0;
            }
            this.updateTarget();
            this.updateFrameNumber();

        }
        const divPost = document.createElement('div');
        setA.appendChild(divPost);
        divPost.textContent = '>>>';
        divPost.style.color = 'aqua';
        divPost.onclick = () => {
            // console.log(this.solution.canvasImage.statusRendering);
            this.currentIndex++;
            if (this.currentIndex > this.timeline.length - 1) {
                this.currentIndex = this.timeline.length - 1
            }
            this.updateTarget();
            this.updateFrameNumber();
        }

        const divAni = document.createElement('div');
        this.div.appendChild(divAni);
        divAni.textContent = 'Play';
        // divRotate.style.position = 'absolute';
        divAni.style.color = 'aqua';
        divAni.onclick = () => {
            this.solution.canvasImage.needInterpolation = !this.solution.canvasImage.needInterpolation;
        }

        let checkBoxUIB = getCheckBox('Easing Interpolation');
        this.div.append(checkBoxUIB.div);
        checkBoxUIB.checkBox.checked = true;
        checkBoxUIB.checkBox.onchange = () => {
            if (checkBoxUIB.checkBox.checked === true) {
                this.solution.canvasImage.easingInterpolation = true;
            } else {
                this.solution.canvasImage.easingInterpolation = false;
            }
        }

        let slider = getSlider('Speed', '0', '100')
        slider.text.textContent = 'Speed: '
        this.div.append(slider.div);
        slider.slider.oninput = () => {
            const val = +((+slider.slider.value) * 0.001  + 0.03).toFixed(3);
            slider.text.textContent = 'Speed: ' + val;
            this.solution.canvasImage.speedInterpolation = val;
        }


        this.divFrameNum = document.createElement('div');
        this.divFrameNum.style.display = 'flex';
        this.div.appendChild(this.divFrameNum);
        this.updateFrameNumber();

        const paragraph = document.createElement('p');
        paragraph.textContent = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/worldmap/worldMap_03.jpg';
        this.div.append(paragraph);
    }
    public updateFrameNumber() {
        while (this.divFrameNum.lastElementChild) {
            this.divFrameNum.removeChild(this.divFrameNum.lastElementChild);
        }
        for(let i = 0; i < this.timeline.length; ++i) {
            if (this.currentIndex === i) {
                this.divFrameNum.appendChild(getP(i, 'aqua'));
            } else {
                this.divFrameNum.appendChild(getP(i, '#999'));
            }
        }

    }
    public updateTarget() {
        this.solution.canvasImage.statusTarget = new Vector(
            this.timeline[this.currentIndex].x,
            this.timeline[this.currentIndex].y,
            this.timeline[this.currentIndex].z,
        );
        this.solution.canvasImage.needInterpolation = true;
    }
}

const getP = (num: number , col: string) => {
    const p = document.createElement('p')
    p.textContent = ` ${num} `;
    p.style.paddingRight = '10px';
    p.style.color = col
    return p;
}
const getSlider = (title: string, min: string = '2009', max: string = '2015') => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = min;
    slider.max = max;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);

    return {
        div, slider, text
    }
};
const getCheckBox = (title: string) => {
    const div = document.createElement('div');
    div.style.display = 'flex';

    const checkBox = document.createElement('input');
    checkBox.type = 'checkbox';
    checkBox.defaultChecked = false;
    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(checkBox);
    div.appendChild(text);

    return {
        div, checkBox
    }
};